<template>
  <div style="padding:20px;">
    <Form ref="sales" :model="sales" :rules="rules" label-position="left" :label-width="120">
      <FormItem label="活动类型">
        <Select v-model="sales.itemmodel"  disabled class="formItem">
          <Option  value='3' >积分商品</Option>
        </Select>
      </FormItem>
      <FormItem label="活动商品" prop="itemid">
        <item-select v-model='sales.itemid' @on-selected='handleSelect'></item-select>
      </FormItem>
      <FormItem label="活动积分" prop="price">
        <InputNumber v-model='sales.price' placeholder='请输入活动价格' :min="0" class="formItem"></InputNumber>
      </FormItem>
      <!-- <FormItem label="活动数量" prop="num" >
        <InputNumber v-model='sales.num'  placeholder='请输入活动数量' :min="0" class="formItem" :precision="0"></InputNumber>
      </FormItem> -->
      <!--<FormItem label="活动时间">
        <DatePicker type="datetimerange" placement="bottom-end" v-model="sales.date"  split-panels class="formItem"></DatePicker>
      </FormItem>-->
      <FormItem label="原始库存">
        <Input v-model='originalNum' disabled class="formItem"></Input>
      </FormItem>
      <FormItem>
        <Button type='primary' @click="handleSave('sales')">
          保存创建
        </Button>
        <Button @click="cancel">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
  export default {
    data() {
      const validateId=(r,v,callback)=>{
        if(v===''){
          callback(new Error('请选择商品'));
        }{
          callback();
        }
      };
      const validatePrice=(r,v,callback)=>{
        if(v===''||v===null){
          callback(new Error('请输入金额'));
        }{
          callback();
        }
      };
      const validateNum=(r,v,callback)=>{
        if(v===''||v===null){
          callback(new Error('请输入数量'));
        }else if(v>this.originalNum){
          callback(new Error('活动数量不能大于库存'));
        }else{
          callback();
        }
      };
      return {
        sales: {
          itemmodel: '3',
          itemid: '',
          //num: 0,
          price: 0,
          ishidden:0,
        /*  date:[new Date(),new Date()]*/
        },
        originalNum:0,
        rules: {
          itemid:[
            { validator: validateId, trigger: 'blur' }
          ],
          price: [
            { validator: validatePrice, trigger: 'blur' }
          ],
          // num: [
          //   { validator: validateNum, trigger: 'blur' }
          // ],
        },
      };
    },
    mounted() {
      this.$sitemap.prvname = "营销活动";
      this.$sitemap.currname = '新建积分商品';
    },
    methods: {
      handleSelect(item) {
        // this.sales.num = item.num;
        this.originalNum = item.num;
      },
      //格式化数据
      formatData(){
        let params={};
        let that=this;
        Object.keys(that.sales).forEach(key => {
          switch (key){
            /*case 'date':
              if(that.sales[key][0]!==''&&that.sales[key][1]!==''){
                params.seckillbegin =Date.parse(that.sales[key][0])/1000;
                params.seckillend	 =Date.parse(that.sales[key][1])/1000;
              }
              break;*/
            default:
              params[key] = that.sales[key];
              break;
          }
        });
        return params;
      },
      //创建提交
      handleSave(name) {
        let that=this;
        that.$refs[name].validate((valid) => {
          if(valid){
            this.$http.post(this.$api.item_editmodel, that.formatData()).then(res => {
              if(res.data.result==1){
                that.$Message.success('添加成功');
                that.$router.push('/points/list');
              }else{
                that.$Message.error(res.data.msg);
              }
            }).catch(err => {
              that.$Message.error('服务器异常');
            })
          }else{
            that.$Message.error('请核对信息');
          }
        });
      },
      cancel(){
        this.$router.push('/points/list');
      }
    },
  }
</script>
<style>
  .formItem{
    width: 350px;
  }
</style>



